.bottom-navbar {
    position: sticky;
    margin: 0 auto; 
    bottom: 30px;
    width: 80%;
    background-color: var(--primary-color);
    display: flex;
    justify-content: space-around;
    align-items: center;
    padding: 10px 0;
    border-radius: var(--border-radius-small);
    box-shadow: 0 4px 20px rgba(0, 0, 0, 0.1);
  }
  
  .nav-item {
    transition: all 0.3s ease;
  }

  .nav-item img{
    width: 22px;
    filter: brightness(0) invert(0.9);
  }
  
  .nav-item.active {
    filter: brightness(0) invert(1);
    transform: scale(1.2);
  }
  
  .nav-item:hover {
    transform: scale(1.1);
  }

  .top-notch {
    position: sticky; 
    top: 10px;
    margin: 0 auto;
    width: 35%;
    height: 30px;
    background-color: black;
    border-radius: var(--border-radius);
    z-index: 1000;
  }