:root {
  /*--primary-color: #9b674a;*/
  --primary-color: #2a9d8f;
  --secondary-color: #264653;
  /*--secondary-color: #4e4d46; */
  --bg-color: #f0f0f0;
  --color-black: #212529;
  --color-white: #f0f0f0;
  --color-box-bg: #e5e5e5;
  --color-3: #e9c46a;
  --color-4: #f4a261;
  --color-5: #e76f51;

  --font-title: 14px;
  --font-subtitle: 10px;

  --border-radius: 36px;
  --border-radius-small: 18px;
  --padding: 16px;
  --margin: 24px;
  --margin-box: 8px;
  --gap: 18px;

  --icon-px: 20px;
}

.text-white {
  color: var(--color-white);
}

@keyframes fadeIn-box {
  from {
    opacity: 0;
    transform: translateY(10px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.fade-in-box {
  animation: fadeIn-box 0.8s ease-in-out;
}

.fade-in-1 {
  animation: fadeIn-box 1.1s ease-in-out;
}

.fade-in-2 {
  animation: fadeIn-box 2.0s ease-in-out;
}


