:root{
    --font-size-details:12px;
}

.flex-horizontal{
    display: flex;
    justify-content: space-between;
    align-items: center;
    gap: 12px;
}

.flex-vertical{
    display: flex;
    flex-direction: column;
    justify-content: center;
    font-size: var(--font-size-details);
    background-color: white;
    height: 70%;
    padding: var(--padding);
    border-radius: var(--border-radius-small);
    gap: 6px;
}

.chart-container {
  width: 100%;
  height: 250px;
}

.recharts-cartesian-axis-tick text {
  font-size: var(--font-size-details);
  fill: #333;
}

.recharts-legend-item text {
  font-size: 14px;
}

.recharts-tooltip-wrapper {
  font-size: 14px;
}

.chart-div {
  background-color: var(--color-box-bg);
  border-radius: var(--border-radius-small);
  padding: 12px 22px;
}

.dot-color-1{
    width: 10px;
    height: 10px;
    background-color: var(--primary-color);
    border-radius: 24px;
}
.dot-color-2{ 
    width: 10px;
    height: 10px;
    background-color: var(--secondary-color);
    border-radius: 24px;
}

.dot-color-3{ 
    width: 10px;
    height: 10px;
    background-color: var(--color-3);
    border-radius: 24px;
}
.dot-color-4{ 
    width: 10px;
    height: 10px;
    background-color: var(--color-4);
    border-radius: 24px;
}
.dot-color-5{ 
    width: 10px;
    height: 10px;
    background-color: var(--color-5);
    border-radius: 24px;
}

.legend p {
    min-width: 50px;

}



