@import url("../../styles/variables.css");

.chip-div {
  display: flex;
  gap: 10px;
  margin: 10px 0;
  overflow-x: auto;
}

.chip-div::-webkit-scrollbar {
  display: none; /* Απόκρυψη scrollbar */
}

.chip {
  background-color: var(--color-box-bg);
  padding: 8px 12px;
  color: black;
  border-radius: var(--border-radius-small);
  margin: 0 auto;
}

.chip.active {
  color: white;
  background-color: var(--primary-color);
}

.details-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  gap: 10px;
}

.details-title {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.date-border {
  padding: 10px 20px;
  background-color: rgb(85, 113, 113);
  color: white;
  border-radius: var(--border-radius-small);
}

.check-time {
  margin-top: var(--margin-box);
}

.detail-box-50 {
  width: 50%;
  background-color: var(--color-box-bg);
  padding: var(--padding);
  border-radius: var(--border-radius-small);

  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.detail-box-50 img {
  width: 30%;
}

.detail-box-50 p {
  padding-top: var(--margin-box);
}

.info-line-title {
  font-size: 12px;
}

.info-line-info {
  font-size: 12px;
  font-weight: bold;
}

.details-person-box {
  background-color: var(--color-box-bg);
  padding: var(--padding);
  border-radius: var(--border-radius-small);
}

.details-hr {
  margin-top: 10px;
  margin-bottom: 10px;
}

.payment-box-completed {
  background-color: rgb(130, 192, 130);
  color: white;
  border-radius: var(--border-radius-small);
  padding: var(--padding);
}

.payment-box-uncompleted {
  background-color: rgb(219, 121, 121);
  color: white;
  border-radius: var(--border-radius-small);
  padding: var(--padding);
}

.payment-img{
  width: 24px;
  filter: brightness(0) invert(1);
}

.timeline-route {
  display: flex;
  align-items: center;
  gap: 12px;
  background-color: var(--color-box-bg);
  padding: var(--padding);
  border-radius: var(--border-radius-small);
}

.timeline-route img{
  width: 18px;
  opacity: 0.8;
}

.icons {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 6px;
}

.circle {
  width: 10px;
  height: 10px;
  border-radius: 50%;
  border: 2px solid rgb(209, 209, 209);
}

.start {
  border-style: dashed;
}

.end {
  background-color: var(--color-box-bg);
}

.plane-icon {
  font-size: 14px;
}

.solid-line {
  width: 2px;
  height: 3vh;
  background-color: rgb(213, 213, 213);
}

.flight-info {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  height: 120px;
  width: 100%;
}

.line-info{
  display: flex;
  width: 100%;
  justify-content: space-evenly;
  align-items: center;
  gap: 5%;
}

.location {
  background-color: var(--primary-color);
  padding: 5px 20px;
  margin: 0;
  border-radius: var(--border-radius-small);
}

.location p {
  margin: 0;
  color: gray;
  font-size: 14px;
}
