@import url('./styles/variables.css');

body {
  font-family: 'Poppins', sans-serif;
}

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

h1{
  font-size: 20px;
  font-weight: bold;
}

h2{
  font-size: 16px;
  font-weight: bold;
}

h3{
  font-size: 14px;
  font-weight: 600;
}

h4{
  font-size: 12px;
  font-weight: 600;
}

p{
  font-size: 10px;
  color: var(--secondary-color);
}

hr{
  margin: 20px 0px;
  color: rgba(192, 192, 192, 0.327);
}

.App {
  text-align: center;
  display: flex;
  justify-content: center;
  align-items: center;
  height: 100vh;
  font-family: 'Poppins', sans-serif;

  background: radial-gradient(circle, #264653, #2a9d8f, #e9c46a);
    background-size: 300% 300%;
    animation: waveBackground 30s infinite alternate ease-in-out;
}

.mobile-Rect{
  height: 90vh;
  aspect-ratio: 420 / 844;

  position: relative;

  background-color: var(--bg-color);
  border-radius: var(--border-radius);
  padding-inline: var(--margin);

  overflow-y: auto;
  scroll-behavior: smooth;
  scrollbar-width: none;
  -ms-overflow-style: none;
  cursor: pointer;
}

.mobile-Rect::-webkit-scrollbar {
  display: none;
}

.text-left-align{
  text-align: left;
}

.component-title{
  padding-bottom: 12px;
}

.reserv-line{
  display: flex;
  align-items: center;
  justify-content: space-between;
  padding: 14px;
  margin-bottom: var(--margin-box);
  border-radius: var(--border-radius-small);
  background-color: var(--color-box-bg);
}

.reserv-line-img-info{
  display: flex;
  align-items: center;
}

.reserv-line-info{
  margin-left: var(--margin-box);
  text-align: left;
}

.arrow-icon{
  width: var(--icon-px);
  height: var(--icon-px);
}

.buttonBack{
  border: 0;
  background-color: transparent;
  padding: 10px 0px;
}

.dotted-background {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: transparent;

  background-image: radial-gradient(rgba(255, 255, 255, 0.239) 2px, transparent 2px);
  background-size: 200px 200px;
  pointer-events: none;
  z-index: 0;
}

@keyframes waveBackground {
  0% {
      background-position: 0% 50%;
  }
  50% {
      background-position: 100% 50%;
  }
  100% {
      background-position: 0% 50%;
  }
}

@media (max-width: 400px) {
  .mobile-Rect {
    width: 100vw;
    height: 100vh;
    border-radius: 0;
    padding: var(--margin);
  }

  .mobile-Rect::-webkit-scrollbar {
    display: none;
  }

  .top-notch {
    display: none;
  }
}
