@import url('../styles/variables.css');

* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

.header {
  margin-top: 12px;
  display: flex;
  justify-content: space-between;
  text-align: end;
}

.horiz-flex {
  margin-top: 32px;
  display: flex;
  justify-content: space-evenly;
  padding: 12px;
  border-radius: var(--border-radius-small);
}

.col-left {
  text-align: start;
}

.header-logo {
  width: 50px;
}

.icon {
  width: var(--icon-px);
  object-fit:contain;
}

.new-booking-div {
  width: 100%;
  margin-top: 12px;
  background-color: var(--secondary-color);
}

.new-booking-text {
  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  text-align: left;
}

.gap {
  width: var(--gap);
}

.card {
  margin-top: var(--margin);
  position: relative;
  width: 100%;
  height: 200px;
  border-radius: var(--border-radius-small);
  overflow: hidden;
}

.card img {
  width: 100%;
  height: 100%;
  object-fit: cover;
}

.info-box {
  position: absolute;
  bottom: 10px;
  left: 10px;
  right: 10px;
  background-color: rgba(255, 255, 255, 0.908);
  padding: 12px;
  border-radius: 14px;

  display: flex;
  flex-direction: column;
  align-items: flex-start;
  justify-content: flex-start;
  text-align: left;
}

.accounts-div{
  padding-top: var(--margin);
}

.accounts-box-half{
  display: flex;
  align-items: center;
  justify-content: space-between;
}

.account-box{
  padding: 14px;
  width: 47.5%;
  background-color: var(--color-box-bg);
  border-radius: var(--border-radius-small);

  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.account-box-line{
  margin-top: 12px;
  width: 100%;
  background-color: var(--color-box-bg);
  border-radius: var(--border-radius-small);

  display: flex;
  flex-direction: column;
  align-items: flex-start;
}

.account-img{
  width: 48px;
  border-radius: 14px;
}
